@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800;900&display=swap);
:root {
  --font: "Montserrat", sans-serif;
  --subBg: #f5f5f5;
  --max-width: 1140px;
}

body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-family: var(--font);
}

button {
  font-family: "Montserrat", sans-serif;
  font-family: var(--font);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding: 1rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 7rem;
}

button {
  cursor: pointer;
  font-weight: 600;
  letter-spacing: 1px;
  font-size: 1rem;
  padding: 0.8rem 1.2rem;
  border: none;
  background: #5159fc;
  color: white;
  border-radius: 5px;
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  outline: none;
  transition: -webkit-transform 400ms;
  transition: transform 400ms;
  transition: transform 400ms, -webkit-transform 400ms;
}

button:hover {
  -webkit-filter: brightness(0.95);
          filter: brightness(0.95);
}

button:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

button i {
  font-weight: normal;
  font-size: 1.5rem;
}

.notfound {
  position: fixed;
  width: calc(100vw - 2rem);
  height: calc(100vh - 2rem);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 1rem;
  top: 0;
  z-index: 1111;
  background: white;
}

.notfound .nofTitle {
  font-weight: 700;
  font-size: 1.5rem;
  opacity: 0.8;
  margin-bottom: 0;
}

.notfound .nofDesc {
  font-weight: normal;
  font-size: 1rem;
  opacity: 1;
  max-width: 50ch;
  text-align: center;
  line-height: 1.8rem;
}

.notfound > i {
  font-size: 10rem;
  color: rgb(255, 98, 87);
}

.notfound .nofAction {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.notfound .nofAction > button:nth-child(1) {
  color: #5159fc;
  background: #e4e6ff;
}

.Info {
  width: 100%;
  max-width: var(--max-width);
}

.list {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.InfoMin {
  width: 90%;
  transition: -webkit-transform 400ms ease;
  transition: transform 400ms ease;
  transition: transform 400ms ease, -webkit-transform 400ms ease;
  cursor: pointer;
}

.InfoMin:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.infoHeader {
  position: relative;
  background: black;
  color: white;
  border-radius: 10px;
  overflow: hidden;
  padding: 3rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  isolation: isolate;
  margin-bottom: 3rem;
}

.infoHeader::before {
  content: "";
  position: absolute;
  inset: 0;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  background: linear-gradient(to bottom, rgba(31, 31, 31, 0.863), transparent);
  z-index: -1;
}

.nameLogo {
  display: flex;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  margin-bottom: 0.5rem;
}

.nameLogo h1 a {
  text-decoration: none;
  margin-left: 1rem;
  font-size: 1.2rem;
  color: white;
  font-weight: normal;
  opacity: 0.5;
}

.nameLogo h1 a:hover {
  opacity: 1;
}

.nameLogo .image {
  position: relative;
  width: 3rem;
  aspect-ratio: 1/1;
  background: rgb(119, 81, 255);
  border-radius: 7px;
  overflow: hidden;
}

.nameLogo .imageT {
  background: transparent;
}

.nameLogo .image img {
  position: absolute;
  inset: 0;
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.description {
  max-width: 60ch;
  line-height: 2rem;
  margin-left: 5rem;
  margin-bottom: 1rem;
  font-size: 0.95rem;
  opacity: 0.8;
}

.creditSection {
  margin-left: 5rem;
  display: flex;
  flex-direction: column;
}

.creditSection h2 {
  margin-bottom: 1rem;
}

.creditItem {
  opacity: 0.8;
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  grid-gap: 0.6rem;
  gap: 0.6rem;
}

.contactBtn {
  text-decoration: none;
  color: white;

  background: rgba(255, 255, 255, 0.068);
  padding: 0.4rem 1rem;
  border: 1px solid white;
  border-radius: 100px;
  font-size: 0.8rem;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.contactBtn:hover {
  background: white;
  color: black;
}

.Work {
  width: 100%;
  max-width: var(--max-width);
  margin-bottom: 12rem;
}

hr {
  opacity: 0.5;
  margin-bottom: 1rem;
}

Input {
  accent-color: #5159fc;
}

input[type="text"] {
  background: #dfe1ff;
  border: 3px solid transparent;
  border-radius: 5px;
  min-width: 15rem;
  width: 100%;
  outline: none;
  padding: 0.7rem 1rem;
  font-size: 1.2rem;
  transition: border 300ms;
}

input[type="text"]::-webkit-input-placeholder {
  color: #7a7fdf;
}

input[type="text"]:-ms-input-placeholder {
  color: #7a7fdf;
}

input[type="text"]::placeholder {
  color: #7a7fdf;
}

input[type="text"]:focus {
  border: 3px solid rgba(43, 43, 43, 0.171);
}

input[type="color"] {
  -webkit-appearance: auto;
          appearance: auto;
  width: 4rem;
  height: 4rem;
  cursor: pointer;
  border-width: 1px;
  border-radius: 5px;
  padding: 1px 3px;
  background: transparent;
}

label,
input:not([type="text"]) {
  cursor: pointer;
}

label {
  margin-left: 0.4rem;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

h3 {
  font-size: 1.5rem;
  margin-bottom: 0.6rem;
}

h3:last-child {
  margin-bottom: 0rem;
}

p {
  font-size: 1rem;
  opacity: 0.5;
  font-weight: 500;
  margin-bottom: 1rem;
}

.loadingBar {
  position: relative;
  background: rgba(43, 43, 43, 0.158);
  height: 1rem;
  border-radius: 100px;
  padding: 5px;
  overflow: hidden;
}

.loading {
  height: 100%;
  border-radius: 100px;
  background: linear-gradient(135deg, #8bc6ec, #9599e2);
  transition: width 1000ms ease;
}

.loadingMsg {
  font-weight: 800;
  margin-top: 1rem;
}

.hint {
  position: relative;
  margin-bottom: 1rem;
  border-radius: 10px;
  overflow: hidden;
  width: 300px;

  aspect-ratio: 2 / 1;
}

.hint::after {
  content: "HINT";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 0.5rem;
  padding-bottom: 2rem;
  color: white;
  font-weight: 700;
  letter-spacing: 5px;
  font-size: 0.7rem;
  opacity: 0.8;
  background: linear-gradient(to bottom, rgba(26, 26, 26, 0.849), transparent);
}

.hint img {
  width: 300px;
  aspect-ratio: 2 / 1;
  object-fit: cover;
}

.config {
  background: #eaeaff;
  border-radius: 5px;
}

.config .description {
  font-weight: 600;
  margin-left: 0;
  line-height: 1.5rem;
  opacity: 0.8;
  padding-bottom: 0rem;
  margin-bottom: 0rem;
}

.downloadBar {
  position: sticky;
  bottom: 0;
  padding: 1rem 1rem;
  background: white;
  display: grid;
  place-content: center;
  width: 100%;
  box-shadow: 0px -7px 10px rgba(0, 0, 0, 0.089);
}

.downloadBar .wrapper {
  max-width: var(--max-width);
  width: calc(100vw - 3.001rem);
  padding: 0rem 1rem;

  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.configMinItem {
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 1rem;
  margin: 1rem 0rem;
  border-radius: 5px;
  background: white;
  transition: background 300ms;
}

.configMinItem.show {
  color: white;
  background: #5159fc;
}

@-webkit-keyframes fadeReveal {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeReveal {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.configMaxItem {
  padding: 1rem;
  display: flex;
  align-items: flex-start;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: space-between;
  -webkit-animation: fadeReveal 500ms ease 0ms forwards;
          animation: fadeReveal 500ms ease 0ms forwards;
}

.inputContainer {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

.configMinItem:hover {
  background: rgba(0, 0, 0, 0.068);
}

.show:hover {
  -webkit-filter: contrast(1.2);
          filter: contrast(1.2);
}

.configMinItem.show:hover {
  background: rgba(55, 65, 255, 0.753);
}

nav {
  color: black;
  background: white;
  box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.068);
}

nav > a {
  color: currentColor;
  text-decoration: none;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: color 300ms;
}

nav > a:hover:nth-child(1),
nav > a:hover:nth-child(5) {
  color: hsl(240, 100%, 74%);
}

nav > a:hover:nth-child(2),
nav > a:hover:nth-child(6) {
  color: rgb(255, 175, 122);
}

nav > a:hover:nth-child(3) {
  color: hsl(327, 100%, 74%);
}

nav > a:hover:nth-child(4) {
  color: hsl(138, 52%, 49%);
}

#deskNav {
  position: fixed;
  top: 0;
  height: 6rem;
  width: calc(100vw - 4rem);

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding: 0rem 2rem;
  grid-gap: 2.5rem;
  gap: 2.5rem;
  z-index: 999;
  transition: background 500ms linear, -webkit-backdrop-filter 500ms linear;
  transition: background 500ms linear, backdrop-filter 500ms linear;
  transition: background 500ms linear, backdrop-filter 500ms linear, -webkit-backdrop-filter 500ms linear;
}

.modal {
  position: fixed;
  inset: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(22, 22, 22, 0.274);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  display: grid;
  place-content: center;
}

.modal .container {
  /* width: inherit; */
  min-height: 200px;
  max-width: 500px;
  max-height: 90vh;
  padding: 1rem;
  background: white;
  border-radius: 5px;
}

.modalHeader {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.modalHeader i {
  border-radius: 5px;
  font-size: 1.8rem;
  cursor: pointer;
}

.modalHeader i:hover {
  background: rgba(0, 0, 0, 0.068);
}

.modal .container h1 {
  display: inline-flex;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  background: linear-gradient(135deg, #239ff1, #333cdd);
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}

.modal .container h1 > i {
  font-weight: normal;
  font-size: 4rem;
  color: rgba(0, 0, 0, 0.199);
}

.modal .container p {
  margin-left: 6rem;
  width: 40ch;
  line-height: 1.8rem;
  opacity: 1;
  font-size: 0.9rem;
}

.modal .container .head {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

li {
  list-style: outside;
  margin-top: 0;
  margin-bottom: 0.5rem;
}

